import React, { useState } from 'react';

function NameEntry({ onSubmit }) {
  const [player1, setPlayer1] = useState('');
  const [player2, setPlayer2] = useState('');

  // Helper function to format names
  const formatName = (name) => {
    if (!name) return '';
    return name.charAt(0).toUpperCase() + name.slice(1).toLowerCase();
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit({
      player1: formatName(player1),
      player2: formatName(player2),
    });
  };

  return (
    <form onSubmit={handleSubmit} className="flex flex-col items-center bg-white dark:bg-gray-600 p-8 rounded-xl shadow-2xl">
      <h2 className="text-3xl font-bold mb-6 text-gray-800 dark:text-gray-200">Enter Player Names</h2>
      <div className="w-full max-w-sm">
        <div className="mb-4">
          <label className="block text-gray-700 dark:text-gray-300 mb-2" htmlFor="player1">
            Player 1 (X):
          </label>
          <input
            type="text"
            id="player1"
            value={player1}
            onChange={(e) => setPlayer1(e.target.value)}
            className="w-full px-3 py-2 border border-gray-400 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 dark:bg-gray-700 dark:text-gray-200"
            placeholder="Enter Player 1 Name"
            aria-label="Player 1 Name"
          />
        </div>
        <div className="mb-6">
          <label className="block text-gray-700 dark:text-gray-300 mb-2" htmlFor="player2">
            Player 2 (O):
          </label>
          <input
            type="text"
            id="player2"
            value={player2}
            onChange={(e) => setPlayer2(e.target.value)}
            className="w-full px-3 py-2 border border-gray-400 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-500 dark:bg-gray-700 dark:text-gray-200"
            placeholder="Enter Player 2 Name"
          />
        </div>
        <button
          type="submit"
          className="bg-blue-500 text-white px-6 py-3 rounded-full text-lg font-semibold hover:bg-blue-600 dark:hover:bg-blue-700 focus:ring-4 focus:ring-blue-200 transition-colors duration-300 shadow-md hover:shadow-lg"
          aria-label="Submit Player Names"
        >
          Start Game
        </button>
      </div>
    </form>
  );
}

export default NameEntry;