import React from 'react';

function ScoreCard({ gameMode, playerNames, score }) {
  return (
    <div className="w-full max-w-md bg-white dark:bg-gray-800 shadow-lg rounded-lg p-6 mb-6 transition-transform duration-300 hover:scale-105">
      <h2 className="text-2xl font-semibold text-gray-800 dark:text-gray-200 mb-4 text-center">
        {gameMode === 'computer' ? 'You vs Computer' : 'Player Scores'}
      </h2>
      <div className="flex justify-around items-center">
        <div className="flex flex-col items-center">
          <span className="text-lg font-medium text-gray-700 dark:text-gray-300">
            {gameMode === 'computer' ? 'You' : playerNames.player1}
          </span>
          <span className="text-2xl font-bold text-blue-500 dark:text-blue-300">
            {score.player1}
          </span>
        </div>
        {gameMode === 'computer' && (
          <div className="flex flex-col items-center">
            <span className="text-lg font-medium text-gray-700 dark:text-gray-300">
              Computer
            </span>
            <span className="text-2xl font-bold text-red-500 dark:text-red-300">
              {score.player2}
            </span>
          </div>
        )}
        {gameMode !== 'computer' && (
          <div className="flex flex-col items-center">
            <span className="text-lg font-medium text-gray-700 dark:text-gray-300">
              {playerNames.player2}
            </span>
            <span className="text-2xl font-bold text-green-500 dark:text-green-300">
              {score.player2}
            </span>
          </div>
        )}
      </div>
    </div>
  );
}

export default ScoreCard;