import React, { useState, useEffect, useRef } from 'react';
import Confetti from 'react-confetti';
import { Howl } from 'howler';
import winSound from './sounds/win.mp3';
import clickSound from './sounds/click.mp3';
import drawSound from './sounds/draw.mp3';
import InstructionsModal from './InstructionsModal';
import ThemeToggle from './ThemeToggle';

// Import the newly created components
import NameEntry from './components/NameEntry';
import ScoreCard from './components/ScoreCard';

// Initialize sounds
const sounds = {
  click: new Howl({ src: [clickSound] }),
  win: new Howl({ src: [winSound] }),
  draw: new Howl({ src: [drawSound] }),
};

// Utility function to calculate winning squares
function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8], // Diagonal from top-left to bottom-right
    [2, 4, 6], // Diagonal from top-right to bottom-left
  ];

  for (let line of lines) {
    const [a, b, c] = line;
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return { winner: squares[a], line };
    }
  }
  return null;
}

// Placeholder for AI move logic
function minimax(squares, depth, isMaximizing) {
  const winner = calculateWinner(squares);
  if (winner) {
    return winner === 'X' ? -1 : 1; // X is the player, O is the AI
  }
  if (squares.every(Boolean)) {
    return 0; // Draw
  }

  if (isMaximizing) {
    let bestScore = -Infinity;
    for (let i = 0; i < squares.length; i++) {
      if (squares[i] === null) {
        squares[i] = 'O'; // AI's turn
        let score = minimax(squares, depth + 1, false);
        squares[i] = null; // Undo move
        bestScore = Math.max(score, bestScore);
      }
    }
    return bestScore;
  } else {
    let bestScore = Infinity;
    for (let i = 0; i < squares.length; i++) {
      if (squares[i] === null) {
        squares[i] = 'X'; // Player's turn
        let score = minimax(squares, depth + 1, true);
        squares[i] = null; // Undo move
        bestScore = Math.min(score, bestScore);
      }
    }
    return bestScore;
  }
}

function getAIMove(squares, difficulty) {
  if (difficulty === 'superhard') {
    let bestScore = -Infinity;
    let move;
    for (let i = 0; i < squares.length; i++) {
      if (squares[i] === null) {
        squares[i] = 'O'; // AI's turn
        let score = minimax(squares, 0, false);
        squares[i] = null; // Undo move
        if (score > bestScore) {
          bestScore = score;
          move = i;
        }
      }
    }
    return move;
  } else {
    // Easy mode: return a random empty square
    const emptySquares = squares.map((val, idx) => val === null ? idx : null).filter(val => val !== null);
    const randomIndex = Math.floor(Math.random() * emptySquares.length);
    return emptySquares[randomIndex];
  }
}

// Position calculation based on board size
function getPosition(index, boardWidth, boardHeight) {
  const row = Math.floor(index / 3);
  const col = index % 3;
  return {
    x: (col + 0.5) * (boardWidth / 3),
    y: (row + 0.5) * (boardHeight / 3),
  };
}

// Square Component
function Square({ value, onClick, highlight }) {
  return (
    <button 
      className={`flex items-center justify-center w-full h-16 text-2xl sm:text-4xl font-bold focus:outline-none rounded-lg shadow-md transition-transform duration-300 hover:scale-105 ${highlight ? 'bg-yellow-300' : 'bg-white dark:bg-gray-600 text-gray-800 dark:text-gray-200'}`}
      onClick={onClick}
      aria-label={`Square ${value ? value : 'empty'}`}
      style={{ height: '90px' }} // Set a fixed height
    >
      {value}
    </button>
  );
}

// Board Component
function Board({ squares, onClick, winningLine, boardSize }) {
  return (
    <div className="relative grid grid-cols-3 gap-1 p-2 bg-gray-200 dark:bg-gray-700 rounded-lg" style={{ width: boardSize.width, height: boardSize.height }}>
      {squares.map((value, i) => (
        <Square 
          key={i} 
          value={value} 
          onClick={() => onClick(i)} 
          highlight={winningLine && winningLine.includes(i)} // Highlight if it's a winning square
        />
      ))}
    </div>
  );
}

// ScoreCard, NameEntry Components are imported above

function Game() {
  const [history, setHistory] = useState([Array(9).fill(null)]);
  const [stepNumber, setStepNumber] = useState(0);
  const [xIsNext, setXIsNext] = useState(true);
  const [gameMode, setGameMode] = useState(null);
  const [difficulty, setDifficulty] = useState(null);
  const [windowSize, setWindowSize] = useState({ width: window.innerWidth, height: window.innerHeight });
  const [score, setScore] = useState(() => {
    const savedScore = localStorage.getItem('tic-tac-toe-score');
    return savedScore ? JSON.parse(savedScore) : { player1: 0, player2: 0 };
  });
  const [playerNames, setPlayerNames] = useState({ player1: '', player2: '' });
  const [nameEntry, setNameEntry] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [confettiActive, setConfettiActive] = useState(false);
  const [confettiColor, setConfettiColor] = useState('#3B82F6'); // Default color
  const [boardSize, setBoardSize] = useState({ width: 300, height: 300 });
  const boardRef = useRef(null);

  // Calculate current squares
  const currentSquares = history[stepNumber];
  const winResult = calculateWinner(currentSquares);
  const winner = winResult ? winResult.winner : null;
  const winningLine = winResult ? winResult.line : null;
  const isDraw = currentSquares.every(Boolean) && !winner;

  let status;
  if (gameMode === 'computer') {
    if (winner === 'X') {
      status = "You win!";
    } else if (winner === 'O') {
      status = "Better luck next time!";
    } else if (isDraw) {
      status = "It's a draw!";
    } else {
      status = `Your turn`;
    }
  } else {
    if (winner) {
      status = `${winner === 'X' ? playerNames.player1 : playerNames.player2} wins!`;
    } else if (isDraw) {
      status = "It's a draw!";
    } else {
      status = `${xIsNext ? playerNames.player1 : playerNames.player2}'s turn`;
    }
  }

  useEffect(() => {
    localStorage.setItem('tic-tac-toe-score', JSON.stringify(score));
  }, [score]);

  useEffect(() => {
    const handleResize = () => {
      setWindowSize({ width: window.innerWidth, height: window.innerHeight });
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    const updateBoardSize = () => {
      if (boardRef.current) {
        const size = Math.min(window.innerWidth * 0.9, 300); // Adjust max size as needed
        setBoardSize({ width: size, height: size });
      }
    };

    updateBoardSize();
    window.addEventListener('resize', updateBoardSize);
    return () => window.removeEventListener('resize', updateBoardSize);
  }, []);

  useEffect(() => {
    const winnerResult = calculateWinner(currentSquares);
    if (winnerResult) {
      sounds.win.play();
      
      // Show confetti only for user wins
      if (gameMode === 'computer' && winnerResult.winner === 'X') {
        triggerConfetti('#3B82F6'); // Color for user win
      } else if (gameMode === 'friend') {
        // Different colors for each player
        const color = winnerResult.winner === 'X' ? '#3B82F6' : '#60A5FA'; // Blue for X, Green for O
        triggerConfetti(color);
      }
      
      if (winnerResult.winner === 'X') {
        setScore(prevScore => ({ ...prevScore, player1: prevScore.player1 + 1 }));
      } else if (winnerResult.winner === 'O') {
        setScore(prevScore => ({ ...prevScore, player2: prevScore.player2 + 1 }));
      }
    } else if (currentSquares.every(Boolean)) {
      sounds.draw.play();
    }
  }, [currentSquares, gameMode]);

  useEffect(() => {
    if (gameMode === 'computer' && !xIsNext && !calculateWinner(currentSquares)) {
      const timer = setTimeout(() => {
        const aiMove = getAIMove(currentSquares, difficulty);
        handleClick(aiMove);
      }, 500);
      return () => clearTimeout(timer);
    }
  }, [xIsNext, gameMode, currentSquares, difficulty]);

  const handleClick = (i) => {
    const currentHistory = history.slice(0, stepNumber + 1);
    const current = currentHistory[currentHistory.length - 1];
    if (calculateWinner(current) || current[i]) return;

    sounds.click.play();

    const squares = current.slice();
    squares[i] = xIsNext ? 'X' : 'O';
    setHistory([...currentHistory, squares]);
    setStepNumber(currentHistory.length);
    setXIsNext(!xIsNext);
  };

  const resetGame = () => {
    setHistory([Array(9).fill(null)]);
    setStepNumber(0);
    setXIsNext(true);
    setConfettiActive(false);
  };

  const startNewGame = () => {
    resetGame();
    setGameMode(null);
    setDifficulty(null);
    setScore({ player1: 0, player2: 0 });
    setPlayerNames({ player1: '', player2: '' });
    setNameEntry(false);
  };

  const handleNameSubmit = (names) => {
    setPlayerNames({
      player1: names.player1 || 'Player X',
      player2: names.player2 || 'Player O'
    });
    setNameEntry(false);
  };

  const startFriendGame = () => {
    setGameMode('friend');
    setNameEntry(true);
  };

  const startComputerGame = () => {
    setGameMode('computer');
    setDifficulty(null);
  };

  const selectDifficulty = (selectedDifficulty) => {
    setDifficulty(selectedDifficulty);
    resetGame();
  };

  const triggerConfetti = (color) => {
    setConfettiActive(true);
    setTimeout(() => setConfettiActive(false), 5000);
    // Store the color in state if needed for rendering
    setConfettiColor(color);
  };

  const renderConfetti = () => {
    if (!confettiActive) return null;

    return (
      <Confetti
        width={windowSize.width}
        height={windowSize.height}
        recycle={false}
        numberOfPieces={200}
        gravity={0.5}
        initialVelocityX={5}
        initialVelocityY={5}
        colors={[confettiColor]} // Use the dynamic color
      />
    );
  };

  const gameContent = () => {
    if (!gameMode) {
      return (
        <div className="flex flex-col items-center bg-white dark:bg-gray-600 p-8 rounded-xl shadow-2xl">
          <h2 className="text-3xl font-bold mb-6 text-gray-800 dark:text-gray-200">Choose Game Mode</h2>
          <button
            className="bg-blue-500 text-white px-6 py-3 rounded-full mb-4 text-lg font-semibold hover:bg-blue-600 dark:hover:bg-blue-700 focus:ring-4 focus:ring-blue-200 transition-colors duration-300 shadow-md hover:shadow-lg"
            onClick={startFriendGame}
            aria-label="Play against a friend"
          >
            Play against a friend
          </button>
          <button
            className="bg-green-500 text-white px-6 py-3 rounded-full text-lg font-semibold hover:bg-green-600 dark:hover:bg-green-700 focus:ring-4 focus:ring-green-200 transition-colors duration-300 shadow-md hover:shadow-lg"
            onClick={startComputerGame}
            aria-label="Play against computer"
          >
            Play against computer
          </button>
          <button
            className="mt-4 bg-transparent text-blue-500 underline"
            onClick={() => setIsModalOpen(true)}
            aria-label="Open Instructions"
          >
            How to Play
          </button>
          <InstructionsModal isOpen={isModalOpen} onClose={() => setIsModalOpen(false)} />
        </div>
      );
    }

    if (gameMode === 'computer' && !difficulty) {
      return (
        <div className="flex flex-col items-center bg-white dark:bg-gray-600 p-8 rounded-xl shadow-2xl">
          <h2 className="text-3xl font-bold mb-6 text-gray-800 dark:text-gray-200">Select Difficulty</h2>
          <button
            className="bg-green-500 text-white px-6 py-3 rounded-full mb-4 text-lg font-semibold hover:bg-green-600 dark:hover:bg-green-700 focus:ring-4 focus:ring-green-200 transition-colors duration-300 shadow-md hover:shadow-lg"
            onClick={() => selectDifficulty('easy')}
            aria-label="Select Easy Difficulty"
          >
            Easy
          </button>
          <button
            className="bg-red-500 text-white px-6 py-3 rounded-full text-lg font-semibold hover:bg-red-600 dark:hover:bg-red-700 focus:ring-4 focus:ring-red-200 transition-colors duration-300 shadow-md hover:shadow-lg"
            onClick={() => selectDifficulty('superhard')}
            aria-label="Select Super Hard Difficulty"
          >
            Super Hard
          </button>
        </div>
      );
    }

    if (nameEntry) {
      return <NameEntry onSubmit={handleNameSubmit} />;
    }

    return (
      <div className={`flex flex-col items-center bg-white dark:bg-gray-600 p-8 rounded-xl shadow-2xl w-full max-w-md ${isDraw ? 'bg-yellow-100 dark:bg-yellow-700' : ''}`}>
        {renderConfetti()}
        <div className="text-3xl font-bold mb-6 text-gray-800 dark:text-gray-200 text-center">
          {status}
        </div>
        {gameMode && <ScoreCard gameMode={gameMode} playerNames={playerNames} score={score} />}
        <div 
          ref={boardRef}
          className={`p-4 rounded-xl shadow-inner ${isDraw ? 'bg-yellow-200 dark:bg-yellow-600' : 'bg-gray-100 dark:bg-gray-700'}`}
        >
          <Board 
            squares={currentSquares} 
            onClick={handleClick} 
            winningLine={winningLine} 
            boardSize={boardSize}
          />
        </div>
        <div className="mt-6 space-x-4 flex flex-wrap justify-center">
          <button
            className="bg-yellow-500 text-white px-4 sm:px-6 py-2 sm:py-3 rounded-full text-lg font-semibold hover:bg-yellow-600 dark:hover:bg-yellow-700 focus:ring-4 focus:ring-yellow-200 transition-colors duration-300 shadow-md hover:shadow-lg m-2"
            onClick={resetGame}
            aria-label="Try Again"
          >
            Try Again
          </button>
          <button
            className="bg-red-500 text-white px-4 sm:px-6 py-2 sm:py-3 rounded-full text-lg font-semibold hover:bg-red-600 dark:hover:bg-red-700 focus:ring-4 focus:ring-red-200 transition-colors duration-300 shadow-md hover:shadow-lg m-2"
            onClick={startNewGame}
            aria-label="New Game"
          >
            New Game
          </button>
        </div>
      </div>
    );
  };

  return (
    <div className="flex flex-col items-center justify-center">
      {gameContent()}
    </div>
  );
}

// App Component
function App() {
  return (
    <div className="min-h-screen flex flex-col items-center justify-center p-4 bg-gray-100 dark:bg-gray-900 transition-colors duration-500 relative">
      <ThemeToggle />
      <h1 className="text-5xl font-bold mb-8 text-transparent bg-clip-text bg-gradient-to-r from-purple-400 via-pink-500 to-red-500">
        Tic-Tac-Toe
      </h1>
      <Game />
    </div>
  );
}

export default App
