import React from 'react';

function InstructionsModal({ isOpen, onClose }) {
  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 transition-opacity duration-300">
      <div className="bg-white dark:bg-gray-800 p-6 rounded-lg shadow-lg max-w-sm w-full transform transition-transform duration-300 scale-100">
        <h2 className="text-2xl font-bold mb-4 text-gray-800 dark:text-gray-200">How to Play</h2>
        <p className="mb-4 text-gray-700 dark:text-gray-300">
          Tic-Tac-Toe is a simple game played on a 3x3 grid. Players take turns placing their mark (X or O) in an empty square. The first player to get three of their marks in a row (vertically, horizontally, or diagonally) wins the game. If all squares are filled without a winner, the game ends in a draw.
        </p>
        <button
          onClick={onClose}
          className="mt-4 bg-blue-500 text-white px-4 py-2 rounded-full hover:bg-blue-600 dark:hover:bg-blue-700 transition-colors duration-300"
          aria-label="Close Instructions"
        >
          Close
        </button>
      </div>
    </div>
  );
}

export default InstructionsModal;